* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: comfortaa;
  font-size: 16px;
}
p,div {
  user-select: none;
}
body{
  width: 100%;
  height: 100vh;
  margin: auto;
  background-color: #fff;
}
body::-webkit-scrollbar {
    width: 8px;
}
body::-webkit-scrollbar-track {
    background: #f1f1f1;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25) inset;
}
body::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: linear-gradient(to right, #338bff5d,#5c9df15d,#338bff5d); 
}
body::-webkit-scrollbar-thumb:hover {
    background: rgba(76, 108, 197, 0.8); 
}
.bg {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  background-image: url('https://armateam.ir/react/Weather/img/weather/sunny.jpg');
  background-position: center;
  background-repeat: repeat;
  background-size: cover;
  background-position-y: -300px;
}

@media only screen and (max-width: 1600px) {
  * {
    font-size: 14px;
  }
}
@media only screen and (max-width: 1400px) {
  * {
    font-size: 12px;
  }
}
@media only screen and (max-width: 1000px) {
  * {
    font-size: 10px;
  }
  .bg {
    background-position-y: -100px;
    background-color: rgba(54, 65, 122, 0.95);
  }
}
@media only screen and (max-width: 600px) {
  * {
    font-size: 18px;
  }
  .bg {
    background-position-y: 0;
    background-color: rgba(54, 65, 122, 0.95);
    background-image: url('https://armateam.ir/react/Weather/img/weather/sunny-mobile.jpg');
  }
}