.cloud {
    width: 250px;
    height: 150px;
}
.shapes {
    position: relative;
    width: 100%;
    height: 100%;
}
.shapes > div {
    position: absolute;
    background-color: rgba(255, 255, 255, 0.4);
    border-radius: 100px;
    backdrop-filter: blur(10px);
}
.circle1 {
    bottom: 0;
    left: 0;
    width: 60%;
    height: 67%;
}
.circle2 {
    bottom: 0;
    right: 12%;
    width: 60%;
    height: 60%;
    clip-path: polygon(100% 100%, 100% 0, 40% 0, 42% 5%, 46% 10%, 50% 20%, 52% 30%, 53% 40%, 53% 50%, 52% 60%, 49.5% 70%, 45% 80%, 39% 90%, 30% 97%, 23% 100% );
}
.circle3 {
    top: 0;
    left: 12%;
    width: 48%;
    height: 80%;
    clip-path: polygon(100% 0, 0 0, 0 45%, 8% 43%, 15% 42%, 65% 43%, 70% 44%, 84% 50%, 100% 50%);
}
.circle4 {
    top: 13.33%;
    right: 24%;
    width: 24%;
    height: 40%;
    clip-path: polygon(100% 0, 0 0, 20% 15%, 23% 25%, 24% 27%, 26% 30%, 35% 66.5%, 80% 67%, 90% 69%, 100% 69%);
}