.main {
    display: flex;
    flex-wrap: wrap;
}
.main-right {
    width: calc(40% - 150px);
    height: calc(100vh - 350px);
    display: flex;
    justify-content:center;
    align-items: center;
}
.main-left {
    width: calc(60% - 350px);
    height: calc(100vh - 350px);
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}
.mobile-menu {
    width: 100%;
    justify-content: space-evenly;
    margin: 20px 0;
    margin-bottom: 40px;
    display: none;
}
.mobile-menu > .menu-icons {
    width: 50%;
    text-align: center;
}
.mobile-menu > .menu-icons > i {
    font-size: 26px;
    padding: 20px;
    border-radius: 50%;
    color: white;
    background-color: rgba(0, 0, 0, 0.1);
    cursor: pointer;
}
.mobile-menu > .menu-icons > i:hover {
    background-color: rgba(255, 255, 255, 0.1);
}
.mobile-menu > .menu-icons > p {
    margin-top: 10px;
    color: #ddd;
    text-shadow: 0 1px 5px #333;
}
.DateView {
    margin: 10% 12%;
}
.DateView > p {
    font-size: 2.5rem;
    color: white;
    text-shadow: 0 1px 5px #333;
}
.CityView {
    cursor: pointer;
    position: relative;
    margin: 10% 12%;
    width: fit-content;
    border: none;
    outline: none;
    background-color: transparent;
}
.city-name {
    display: flex;
    align-items: center;
    border-radius: 15px;
    padding: 10px 12px;
    background-color: rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
}
.city-name:hover {
    background-color: rgba(238, 238, 238, 0.1);
}
.city-name > i {
    font-size: 2.5rem;
    color: white;
    margin: 0 20px;
    cursor: pointer;
    transition: 0.5s;
}
.CityView:focus .angle-open {
    transform: rotateX(180deg);
}
.city-name > .this-city {
    font-size: 2.5rem;
    color: white;
}
.CityView > .city-list {
    position: absolute;
    bottom: 65px;
    right: 0;
    background-color: rgba(255, 255, 255, 0.9);
    box-shadow: 0 2px 10px rgba(34, 34, 34, 0.2);
    border-radius: 15px;
    width: fit-content;
    height: 0;
    overflow: hidden;
    opacity: 0;
    transition: height 0.25s;
    z-index: 1;
}
.CityView:focus .city-list-open {
    opacity: 1;
}
.CityView > .city-list > div {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 10px;
    padding: 8px 12px;
    border-radius: 10px;
}
.city-list > div:first-child {
    margin-top: 10px;
}
.city-list > div:last-child {
    margin-bottom: 10px;
}
.city-list > div:hover {
    background-color: #ddd;
}
.CityView > .city-list > div > p {
    font-size: 16px;
    color: #333;
    text-align: left;
    width: 100%;
    margin-left: 10px;
}

@media only screen and (max-width: 1600px) {
    .main-right {
        width: calc(40% - 100px);
        height: calc(100vh - 300px);
    }
    .main-left {
        width: calc(60% - 300px);
        height: calc(100vh - 300px);
    }
    .CityView > .city-list {
        bottom: 60px;
    }
    .CityView > .city-list > div > p {
        font-size: 16px;
    }
}
@media only screen and (max-width: 1400px) {
    .main-right {
        width: calc(40% - 75px);
    }
    .main-left {
        width: calc(60% - 275px);
    }
    .CityView > .city-list > div > p {
        font-size: 16px;
    }
    .CityView > .city-list {
        bottom: 55px;
    }
}
@media only screen and (max-width: 1000px) {
    .CityView > .city-list {
        bottom: 50px;
    }
    .main-right {
        width: calc(40% - 50px);
        height: calc(100vh - 220px);
    }
    .main-left {
        width: calc(60% - 220px);
        height: calc(100vh - 220px);
    }
}
@media only screen and (max-width: 600px) {
    .main-right {
        width: 0;
        height: 0;
    }
    .main-left {
        width: 100%;
        height: fit-content;
        flex-direction: row;
        align-items: center;
    }
    .mobile-menu {
        display: flex;
    }
    .DateView {
        margin: 30px;
    }
    .DateView > p {
        font-size: 1rem;
        color: white;
    }
    .CityView {
        cursor: pointer;
        position: relative;
        margin: 30px;
        width: fit-content;
        height: fit-content;
    }
    .city-name {
        background-color: rgba(0, 6, 39, 0.2);
        backdrop-filter: blur(0);
        -webkit-backdrop-filter: blur(0);
    }
    .city-name > i:last-child {
        transform: rotateX(180deg);
    }
    .CityView:focus .angle-open-mobile {
        transform: rotateX(0deg) !important;
    }
    .city-name {
        padding: 12px 0;
    }
    .city-name:hover {
        background-color: rgba(238, 238, 238, 0.1);
    }
    .city-name > i {
        font-size: 1rem;
        color: white;
        margin: 0 20px;
        cursor: pointer;
    }
    .city-name > .this-city {
        font-size: 1rem;
        color: white;
    }
    .CityView > .city-list {
        position: absolute;
        top: 45px;
        left: 2px;
        background-color: rgba(255, 255, 255, 0.9);
        box-shadow: 0 2px 10px rgba(34, 34, 34, 0.2);
        border-radius: 15px;
        width: fit-content;
        height: 0;
        overflow: hidden;
        transition: height 0.5s;
    }
    .CityView > .city-list > div > p {
        font-size: 16px;
    }
}