.sidebar {
    width: 500px;
    height: calc(100vh - 350px);
    background-color: rgba(25, 25, 25, 0.3);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    box-shadow: 0 2px 20px rgba(51, 51, 51, 0.4);
    position: fixed;
    top: 0;
    right: 0;
    transition: width 0.5s;
}
.sidebar > i {
    margin: 20px 0 0 20px;
    font-size: 20px;
    color: white;
    display: none;
    cursor: pointer;
    padding: 15px;
    border-radius: 50%;
    width: 50px;
    height: 50px;
}
.sidebar > i:hover {
    background-color: rgba(255, 255, 255, 0.2);
}
.sidebar > .title {
    text-align: center;
    font-size: 2rem;
    background: linear-gradient(to left, rgb(255, 255, 255), #ae73fc);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    color: #ae73fc;
    width: 100%;
    line-height: 80px;
    font-family: 'Oxygen';
}
.sidebar > .textbox{
    height: 60px;
    margin: 0 30px;
    background-color: transparent;
    border: 2px solid rgba(255, 255, 255, 0.4);
    border-radius: 40px;
    box-shadow: 0 1px 10px rgba(51, 51, 51, 0.4);
}
.sidebar > .textbox > i{
    line-height: 56px;
    margin: 0 10px 0 20px;
    font-size: 1.6rem;
    float: left;
    color: #eee;
}
.sidebar > .textbox > .SearchBox{
    width: calc(100% - 100px);
    line-height: 56px;
    background-color: transparent;
    color: #eee;
    padding: 0 10px;
    border:none;
    float: left;
    font-size: 1.2rem;
}
.sidebar > .textbox > .SearchBox:focus{
    outline: none;
}
.sidebar > .textbox > .SearchBox::placeholder {
    color: #ddd;
}
.location {
    width: 100%;
    height: calc(100% - 170px);
    margin: 30px 0;
    overflow-y: auto;
}
.location::-webkit-scrollbar {
    width: 8px;
}
.location::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.2);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25) inset;
}
.location::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: linear-gradient(to right, #338bff5d,#5c9df15d,#338bff5d); 
}
.location::-webkit-scrollbar-thumb:hover {
    background: rgba(76, 108, 197, 0.8); 
}
.location > .search-result {
    height: 0;
    overflow: hidden;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 15px;
    width: calc(100% - 60px);
    color: white;
    margin: auto;
}
.location > .search-result-open {
    height: auto;
    padding-bottom: 20px;
    margin-bottom: 20px;
}
.location > .search-result > p {
    font-size: 1.2rem;
    color: white;
    padding: 16px 20px;
}
.location > .search-result > .city {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 20px;
    padding: 0 15px;
    border-radius: 20px;
}
.location > .search-result > .city > .icon-city {
    cursor: pointer;
}
.location > .search-result > .city:hover {
    background-color: rgba(170, 170, 170, 0.5);
}
.location > .search-result > .city > p {
    padding: 8px 12px;
    margin: 0 10px;
    width: 100%;
}
.location > .my-cities {
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 15px;
    width: calc(100% - 60px);
    padding-bottom: 20px;
    color: white;
    margin: auto;
}
.location > .my-cities > p {
    font-size: 1.2rem;
    padding: 16px 20px;
    display: flex;
    align-items: center;
}
.location > .my-cities > p > span {
    font-size: 0.8rem;
    margin-left: 8px;
}
.location > .my-cities > .city {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 20px;
    padding: 0 15px;
    border-radius: 20px;
}
.location > .my-cities > .city > .icon-city {
    cursor: pointer;
}
.location > .my-cities > .city:hover {
    background-color: rgba(170, 170, 170, 0.5);
}
.location > .my-cities > .city > p {
    padding: 8px 12px;
    border-radius: 10px;
    margin: 0 10px;
    width: 100%;
}

@media only screen and (max-width: 1600px) {
    .sidebar {
        width: 400px;
        height: calc(100vh - 300px);
    }
}
@media only screen and (max-width: 1300px) {
    .sidebar {
        width: 350px;
        height: calc(100vh - 300px);
    }
}
@media only screen and (max-width: 1400px) {
    .sidebar {
        width: 300px;
        height: calc(100vh - 300px);
    }
}
@media only screen and (max-width: 1000px) {
    .sidebar {
        width: 270px;
        height: calc(100vh - 220px);
    }
    .sidebar > .textbox{
        height: 40px;
        margin: 0 30px;
        border: 1px solid rgba(255, 255, 255, 0.4);
        box-shadow: 0 1px 10px rgba(51, 51, 51, 0.2);
    }
    .sidebar > .textbox > i{
        line-height: 38px;
        margin: 0 0 0 15px;
        font-size: 1.4rem;
    }
    .sidebar > .textbox > .SearchBox{
        width: calc(100% - 40px);
        line-height: 38px;
    }
    .location {
        height: calc(100% - 140px);
        margin: 20px 0;
    }
    .location > .search-result > p {
        padding-bottom: 10px;
    }
    .location > .search-result > .city {
        margin: 0 15px;
    }
    .location > .search-result > .city > p {
        padding: 8px 10px;
        margin: 0 8px;
    }
    .location > .my-cities > p {
        padding-bottom: 10px;
    }
    .location > .my-cities > .city {
        margin: 0 15px;
    }
    .location > .my-cities > .city > p {
        padding: 8px 10px;
        margin: 0 8px;
    }
}
@media only screen and (max-width: 600px) {
    .sidebar {
        position: fixed;
        top: 0;
        left: 0;
        width: 0;
        overflow: hidden;
        white-space: nowrap;
        height: 100%;
        z-index: 1;
        background-color: rgba(54, 65, 122, 0.95);
        backdrop-filter: blur(0);
        -webkit-backdrop-filter: blur(0);
    }
    .sidebar-open {
        width: 100%;
    }
    .sidebar > i {
        display: block;
    }
    .sidebar > .title {
        display: none;
    }
    .sidebar > .textbox{
        height: 60px;
        margin: 15px 30px;
        border: 2px solid rgba(255, 255, 255, 0.4);
        box-shadow: 0 1px 10px rgba(51, 51, 51, 0.4);
    }
    .sidebar > .textbox > i{
        line-height: 56px;
        margin: 0 10px 0 20px;
        font-size: 1.6rem;
    }
    .sidebar > .textbox > .SearchBox{
        width: calc(100% - 100px);
        line-height: 56px;
    }
    .location > .search-result > p {
        padding: 16px 20px;
    }
    .location > .search-result > .city {
        margin: 0 20px;
    }
    .location > .search-result > .city > p {
        padding: 8px 12px;
        margin: 0 10px;
    }
    .location > .my-cities > p {
        padding: 16px 20px;
    }
    .location > .my-cities > .city {
        margin: 0 20px;
    }
    .location > .my-cities > .city > p {
        padding: 8px 12px;
        margin: 0 10px;
    }
}