.panel {
    width: 100%;
    height: 350px;
    position: fixed;
    bottom: 0;
    right: 0;
    background-color: rgba(25, 25, 25, 0.3);
    box-shadow: 0 2px 20px rgba(51, 51, 51, 0.5);
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
}
.shape {
    height: 100%;
    width: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.sun-cloudy {
    width: 250px;
    height: 230px;
    position: relative;
}
.sun {
    width: 60% !important;
    height: 65.22% !important;
    position: absolute;
    top: 4%;
    left: 10%;
}
.cloud {
    width: 100% !important;
    height: 65.22% !important;
    position: absolute;
    bottom: 5px;
    left: 10%;
}
.shape > .shape-descripe {
    text-align: center;
    padding: 0 10px;
    margin-top: 10px;
    font-size: 2rem;
    color: #fff;
    text-shadow: 0 1px 10px #777;
}
.panel > .temp {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.panel > .temp > .max-min {
    display: flex;
    flex-direction: column;
    gap: 50px;
}
.panel > .temp > .max-min > div {
    display: flex;
    align-items: center;
    gap: 20px;
}
.panel > .temp > .max-min > div > p {
    display: inline-block;
    font-size: 3rem;
    color: #fff;
    text-shadow: 0 1px 10px #777;
}
.panel > .temp > .max-min > div > i {
    display: inline-block;
    font-size: 2rem;
    color: rgba(192, 223, 248, 0.5);
    text-shadow: 0 1px 10px #777;
}
.vert-line {
    width: 60px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
    margin: 0 30px;
    box-shadow: 0 1px 5px #333;
}
.panel > .temp > p {
    font-size: 2rem;
    color: #fff;
    text-shadow: 0 1px 10px #777;
}
.panel > .weather-data {
    display: flex;
    width: 45%;
    height: 100%;
    align-items: center;
    justify-content: space-evenly;
}
.panel > .weather-data > div {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.panel > .weather-data > div > img {
    height: 100px;
    width: 100px;
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 50%;
}
.panel > .weather-data > div > .data-name {
    font-size: 1.2rem;
    margin-top: 10px;
    margin-bottom: 20px;
    color: #fff;
    text-shadow: 0 0 10px #000;
}
.panel > .weather-data > div > .data-value {
    margin: 20px 0;
    color: #fff;
    text-shadow: 0 1px 10px #777;
    font-size: 2.5rem;
}

@media only screen and (max-width: 1600px) {
    .panel {
        width: 100%;
        height: 300px;
    }
    .sun-cloudy {
        width: 200px;
        height: 184px;
    }
    .panel > .weather-data > div > img {
        height: 80px;
        width: 80px;
    }
}
@media only screen and (max-width: 1400px) {
    .panel {
        width: 100%;
        height: 300px;
    }
    .sun-cloudy {
        width: 180px;
        height: 165.6px;
    }
    .vert-line {
        width: 30px;
        margin: 0 15px;
    }
}
@media only screen and (max-width: 1000px) {
    .panel {
        width: 100%;
        height: 220px;
    }
    .sun-cloudy {
        width: 120px;
        height: 110px;
    }
    .panel > .temp > p {
        font-size: 1.5rem;
    }
    .panel > .weather-data > div > img {
        height: 60px;
        width: 60px;
        padding: 15px;
        background-color: rgba(255, 255, 255, 0.2);
        border-radius: 50%;
    }
}
@media only screen and (max-width: 600px) {
    .panel {
        position: static;
        width: 100%;
        height: auto;
        flex-direction: column;
        background-color: rgba(0, 6, 39, 0.6);
        backdrop-filter: blur(0);
        -webkit-backdrop-filter: blur(0);
    }
    .shape { 
        margin: 50px 0;
    }
    .sun-cloudy {
        width: 250px;
        height: 230px;
    }
    .shape > .shape-descripe {
        font-size: 1.5rem;
    }
    .panel > .temp {
        width: 100%;
        flex-direction: column-reverse;
        margin: 0;
        margin-bottom: 60px;
    }
    .panel > .temp > .max-min {
        width: 100%;
        flex-direction: row;
        justify-content: space-evenly;
    }
    .panel > .temp > .max-min > div {
        gap: 10px;
    }
    .panel > .temp > .max-min > div > p {
        font-size: 2.5rem;
    }
    .panel > .temp > .max-min > div > i {
        font-size: 1.5rem;
    }
    .vert-line {
        display: none;
    }
    .panel > .temp > p {
        font-size: 1.5rem;
        margin: 0;
        margin-bottom: 20px;
    }
    .panel > .weather-data {
        width: 100%;
        height: auto;
        margin-bottom: 50px;
    }
    .panel > .weather-data > div > img {
        height: 40px;
        width: 40px;
        padding: 5px;
    }
    .panel > .weather-data > div > .data-name {
        font-size: 0.8rem;
        margin-top: 15px;
        margin-bottom: 0;
    }
    .panel > .weather-data > div > .data-value {
        margin: 20px 0;
        color: #fff;
        text-shadow: 0 1px 10px #777;
        font-size: 1.5rem;
    }
}
@media only screen and (max-width: 400px) {
    .sun-cloudy {
        width: 200px;
        height: 184px;
    }
    .panel > .temp > .max-min > div > p {
        font-size: 2rem;
    }
    .panel > .temp > .max-min > div > i {
        font-size: 1.5rem;
    }
    .vert-line {
        display: none;
    }
    .panel > .temp > p {
        font-size: 1.2rem;
        margin: 20px;
    }
    .panel > .weather-data {
        flex-direction: column;
        gap: 20px;
        padding: 0 20px;
    }
    .panel > .weather-data > div {
        width: 100%;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }
    .panel > .weather-data > div > .data-name {
        font-size: 0.8rem;
        margin: 0;
        text-align: left;
        width: 100%;
        padding-left: 15px;
    }
    .panel > .weather-data > div > .data-value {
        font-size: 1.2rem;
        margin: 0;
    }
}