.calendar {
    width: 350px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    border-radius: 20px;
    transition: 0.5s;
}
.calendar > i {
    position: absolute;
    font-size: 20px;
    color: white;
    top: 20px;
    left: 20px;
    display: none;
    cursor: pointer;
    padding: 15px;
    border-radius: 50%;
}
.calendar > i:hover {
    background-color: rgba(255, 255, 255, 0.2);
}
.month {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 10px;
    margin-bottom: -10px;
    padding: 10px;
}
.month > p {
    color: #eee;
    margin: 10px;
}
.month > i {
    color: #eee;
    width: 30px;
    line-height: 30px;
    text-align: center;
    border-radius: 50%;
    cursor: pointer;
}
.month > i:hover {
    background-color: rgba(255, 255, 255, 0.2);
}
.calendar > .days {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content:center;
    gap: 0;
    width: 100%;
    height: fit-content;
    scroll-behavior: smooth;
    padding: 20px;
}
.calendar > .days > div {
    text-align: center;
    width: 40px;
    height: 40px;
    line-height: 42px;
    border-radius: 30px;
    margin: 5px;
    font-size: 1.2rem;
    color: #eee;
    cursor: pointer;
}
.calendar > .days > div:hover {
    background-color: rgba(255, 255, 255, 0.2);
}
.span-selected {
    background-color: rgba(255, 255, 255, 0.2);
}

.days::-webkit-scrollbar {
    display: none;
}

@media only screen and (max-width: 1600px) {
    .calendar {
        width: 280px;
    }
    .calendar > .days {
        padding: 15px;
    }
    .calendar > .days > div {
        text-align: center;
        width: 30px;
        height: 30px;
        line-height: 32px;
    }
}
@media only screen and (max-width: 1400px) {
    .calendar {
        width: 250px;
    }
    .calendar > .days {
        padding: 15px;
    }
    .calendar > .days > div {
        text-align: center;
        width: 25px;
        height: 25px;
        line-height: 27px;
    }
}
@media only screen and (max-width: 1000px) {
    .calendar {
        width: 210px;
    }
    .calendar > .days {
        padding: 6px;
    }
    .calendar > .days > div {
        text-align: center;
        width: 25px;
        height: 25px;
        line-height: 26px;
        margin: 3px;
    }
    .month {
        margin-top: 6px;
    }
}
@media only screen and (max-width: 600px) {
    .calendar {
        position: fixed;
        top: 0;
        left: 0;
        z-index: 1;
        width: 0;
        overflow: hidden;
        white-space: nowrap;
        height: 100%;
        justify-content: center;
        border-radius: 0;
        background-color: rgba(54, 65, 122, 0.95);
        backdrop-filter: blur(0);
        -webkit-backdrop-filter: blur(0);
        border-radius: 0;
    }
    .calendar-open {
        width: 100%;
    }
    .calendar > i {
        display: block;
    }
    .calendar > .days {
        /* width: max(100%, 350px); */
        padding: 25px;
    }
    .calendar > .days > div {
        text-align: center;
        width: 50px;
        height: 50px;
        line-height: 52px;
    }
}